.jumbo_text {
  font-size: 182px;
  margin: 0px 0px 100px;
}

.poll_question_container {
  border: 1px solid #d4d4d4;
  color: white;
  display: block;
  background-image: url('https://cdn3.vox-cdn.com/uploads/chorus_asset/file/4387649/Golden-Gate-Bridge_-San-Francisco_-CA.-Creative-Commons-by-Thomas-Hawk..0.jpg');
  background-size: cover;
}

.card {
  transition: all linear .5s;
  padding: 50px;
  border: 1px solid rgba(255, 255, 255, 1);
  box-shadow: 0 10px 10px rgba(0, 0, 0, .25);
  display: inline-block;
  flex-direction: column;
  background: rgba(0, 107, 148, .25);
  border-radius: 0px;
}

.card:hover {
  transition: all linear .5s;
  border: 1px solid rgba(255, 255, 255, .75);
  background: rgba(0, 107, 148, .30);
  box-shadow: 0 7px 1px rgba(0, 0, 0, .25);
}

.card-overlay {
  background-color: rgba(0, 0, 0, .750);
  width: 100%;
  height: 100%;
}

.background_overlay {
  background-color: rgba(0, 0, 0, .750);
  width: 100vw;
  width: 100%;
  min-height: 100vh;
}

.ghost-botton {
  border: 1px solid #fff;
  padding: 10px 40px;
  color: white;
  margin: 20px 20px;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 0px;
}

.poll-response {
  display: flex;
    /*flex-direction: column;*/
  flex-wrap: wrap;
}

.poll-question {
  text-align: left;
  font-size: 34px;
}

.stats_container {
  display: inline-block;
  margin: 50px 0px 250px;
}

.hide {
  display: none;
}

.the_other_hide {
  width: 0px;
  height: 0px
}

.dropdown-menu {
    /*color: #fff;*/
    /*position: relative;*/
}

@media only screen and (max-width: 480px) {
  .jumbo_text {
    font-size: 82px;
    margin: 0px 0px 50px;
  }

  .poll-question {
    text-align: left;
    font-size: 27px;
  }

  .card {
    padding: 15px;
  }
}

.fixedPosition {
  position: fixed;
}
